<script setup>
import { ref } from 'vue'
import axios from 'axios'
import qs from 'qs'

const currentSlide = ref(0)
let field_gender = ref('')
let field_age = ref('')
let field_condition = ref('')
let form_response = ref(null)
let products_category = ref('')
let products_message = ref('')
let products_response = ref(null)
let currentMessage = ref('')
let messages = ref([])
let loading = ref(false)

async function submitMessage() {
  if (currentMessage.value === '') return

  const messageToSend = currentMessage.value
  currentMessage.value = ''

  loading.value = true

  const d = new Date()
  const options = {
    hour: 'numeric',
    minute: 'numeric',
    second: 'numeric',
    hour12: true,
  }
  const n = d.toLocaleTimeString('en-US', options)
  const nLower = n.toLowerCase()

  // generate a random 6-digit number
  const uuid = Math.floor(100000 + Math.random() * 900000)

  messages.value.push({
    id: uuid,
    message: messageToSend,
    from: 'user',
    timestamp: nLower,
  })

  // Auto scroll to bottom of the #messages div in a timeout
  setTimeout(() => {
    const messagesDiv = document.getElementById('messages')
    messagesDiv.scrollTop = messagesDiv.scrollHeight
  }, 100)

  let timeoutId = setTimeout(() => {
    loading.value = false
    messages.value.push({
      id: Math.floor(100000 + Math.random() * 900000),
      message: 'Temporarily Unavailable',
      from: 'ai',
      timestamp: new Date()
        .toLocaleTimeString('en-US', {
          hour: 'numeric',
          minute: 'numeric',
          second: 'numeric',
          hour12: true,
        })
        .toLowerCase(),
    })
    const messagesDiv = document.getElementById('messages')
    messagesDiv.scrollTop = messagesDiv.scrollHeight
  }, 30000)

  var data = qs.stringify({
    message: messageToSend,
  })

  try {
    await axios
      .post('https://chatbot-mvp-ptvum.ondigitalocean.app/api/openai', data, {
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded',
        },
      })
      .then((response) => {
        clearTimeout(timeoutId)

        const d2 = new Date()
        const options2 = {
          hour: 'numeric',
          minute: 'numeric',
          second: 'numeric',
          hour12: true,
        }
        const n2 = d2.toLocaleTimeString('en-US', options2)
        const nLower2 = n2.toLowerCase()

        messages.value.push({
          id: Math.floor(100000 + Math.random() * 900000),
          message: response.data,
          from: 'ai',
          timestamp: nLower2,
        })

        setTimeout(() => {
          const messagesDiv = document.getElementById('messages')
          messagesDiv.scrollTop = messagesDiv.scrollHeight
        }, 100)

        loading.value = false
      })
  } catch (error) {
    console.log(error.response)
  }
}

async function submitForm() {
  const response = await axios.post(
    'https://chatbot-mvp-ptvum.ondigitalocean.app/api/arfinn',
    {
      gender: field_gender.value,
      age: field_age.value,
      condition: field_condition.value,
    }
  )
  form_response.value = response.data
}

async function submitProducts() {
  const response = await axios.post(
    'https://chatbot-mvp-ptvum.ondigitalocean.app/api/products',
    {
      category: products_category.value,
      message: products_message.value,
    }
  )
  products_response.value = response.data
}

function resetForm() {
  form_response.value = null
  field_gender.value = ''
  field_age.value = ''
  field_condition.value = ''
  field_message.value = ''
}

function resetProducts() {
  products_response.value = null
  products_category.value = ''
  products_message.value = ''
}
</script>

<template>
  <div id="window">
    <div id="top-main"
         v-if="currentSlide === 0">
      <div class="main-head">
        <h1>Hi, I'm Arfinn!</h1>
        <svg @click="$emit('toggleWindow')"
             xmlns="http://www.w3.org/2000/svg"
             fill="none"
             viewBox="0 0 24 24"
             stroke-width="2"
             stroke="white"
             class="w-6 h-6">
          <path stroke-linecap="round"
                stroke-linejoin="round"
                d="M6 18L18 6M6 6l12 12" />
        </svg>
      </div>
      <p class="subtitle">I'm a specialized AI bot that can help answer any of your questions about Arfinnmed.</p>
    </div>
    <div id="top-main"
         v-if="currentSlide === 1">
      <div class="flex flex-row items-center space-x-4">
        <div class="rounded-md w-6 h-6 bg-transparent hover:bg-arfinnPurple-800 flex items-center justify-center cursor-pointer"
             @click="currentSlide = 0">
          <svg xmlns="http://www.w3.org/2000/svg"
               fill="none"
               viewBox="0 0 24 24"
               stroke-width="2"
               stroke="white"
               class="w-4 h-4">
            <path stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M15.75 19.5L8.25 12l7.5-7.5" />
          </svg>
        </div>
        <h2>No problem!</h2>
      </div>
      <p class="subtitle">
        Tell us a little bit more about yourself and your condition and we'll scour thousands of pages of clinical research to see what has worked best for others like you.
      </p>
    </div>
    <div id="top-main"
         v-if="currentSlide === 2">
      <div class="flex flex-row items-center space-x-4">
        <div class="rounded-md w-6 h-6 bg-transparent hover:bg-arfinnPurple-800 flex items-center justify-center cursor-pointer"
             @click="currentSlide = 0">
          <svg xmlns="http://www.w3.org/2000/svg"
               fill="none"
               viewBox="0 0 24 24"
               stroke-width="2"
               stroke="white"
               class="w-4 h-4">
            <path stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M15.75 19.5L8.25 12l7.5-7.5" />
          </svg>
        </div>
        <h2>Browse products</h2>
      </div>
      <p class="subtitle">
        Quickly search and browse available products in stock in dispensaries near you.
      </p>
    </div>
    <div id="top-main"
         v-if="currentSlide === 3">
      <div class="flex flex-row items-center space-x-4">
        <div class="rounded-md w-6 h-6 bg-transparent hover:bg-arfinnPurple-800 flex items-center justify-center cursor-pointer"
             @click="currentSlide = 0">
          <svg xmlns="http://www.w3.org/2000/svg"
               fill="none"
               viewBox="0 0 24 24"
               stroke-width="2"
               stroke="white"
               class="w-4 h-4">
            <path stroke-linecap="round"
                  stroke-linejoin="round"
                  d="M15.75 19.5L8.25 12l7.5-7.5" />
          </svg>
        </div>
        <h2>Chat with us</h2>
      </div>
      <p class="subtitle">
        Simply type in your question below to get an immediate answer!
      </p>
    </div>

    <div class="p-4 flex flex-col space-y-2"
         v-if="currentSlide === 0">
      <div class="big-button"
           @click="currentSlide = 1">
        <p class="button-title">Need relief from a medical condition?</p>
        <p class="button-subtitle">I can find a product that works best for you!</p>
      </div>
      <div class="big-button"
           @click="currentSlide = 2">
        <p class="button-title">Browse available products...</p>
        <p class="button-subtitle">Quickly find available products in stock!</p>
      </div>
      <div class="big-button"
           @click="currentSlide = 3">
        <p class="button-title">Ask me anything!</p>
        <p class="button-subtitle">Go ahead, ask me anything!</p>
      </div>
    </div>

    <div class="p-4 flex flex-col flex-1 space-y-2"
         v-if="currentSlide === 1 && form_response === null">
      <select placeholder="Gender"
              class="text-input"
              v-model="field_gender">
        <option value=""
                disabled
                selected>Gender</option>
        <option value="male">Male</option>
        <option value="female">Female</option>
        <option value="other">Other</option>
      </select>
      <select placeholder="Age Range"
              class="text-input"
              v-model="field_age">
        <option value=""
                disabled
                selected>Age Range</option>
        <option value="18-25">18-25</option>
        <option value="26-35">26-35</option>
        <option value="36-45">36-45</option>
        <option value="46+">46+</option>
      </select>
      <select placeholder="Condition"
              class="text-input"
              v-model="field_condition">
        <option value=""
                disabled
                selected>Condition</option>
        <option value="alpha">Alpha</option>
        <option value="bravo">Bravo</option>
        <option value="charlie">Charlie</option>
      </select>
      <div class="flex-1"></div>
      <button class="primary-btn"
              @click="submitForm">Submit</button>
    </div>
    <div class="p-4 flex flex-col flex-1 space-y-2"
         v-if="currentSlide === 1 && form_response !== null">
      <p class="text-sm text-slate-700">{{ form_response }}</p>
      <div class="flex-1"></div>
      <button class="primary-btn"
              @click="resetForm">Start Over</button>
    </div>

    <div class="p-4 flex flex-col flex-1 space-y-2"
         v-if="currentSlide === 2 && products_response === null">
      <select placeholder="Category"
              class="text-input"
              v-model="products_category">
        <option value=""
                disabled
                selected>Category</option>
        <option value="alpha">Alpha</option>
        <option value="bravo">Bravo</option>
        <option value="charlie">Charlie</option>
      </select>
      <textarea placeholder="Your message"
                class="text-input flex-1"
                v-model="products_message" />
      <button class="primary-btn"
              @click="submitProducts">Submit</button>
    </div>
    <div class="p-4 flex flex-col flex-1 space-y-2"
         v-if="currentSlide === 2 && products_response !== null">
      <p class="text-sm text-slate-700">{{ products_response }}</p>
      <div class="flex-1"></div>
      <button class="primary-btn"
              @click="resetProducts">Start Over</button>
    </div>

    <div id="messages"
         class="p-4 flex flex-col flex-1 space-y-4 overflow-y-scroll relative"
         v-if="currentSlide === 3">
      <div v-for="message in messages"
           :key="message.id"
           :class="'flex flex-col wrapper-'+message.from">
        <div :class="['message', message.from]">
          <p class="message-text">{{ message.message }}</p>
        </div>
        <span v-if="message.from === 'user'"
              :class="message.from">You</span>
        <span v-if="message.from === 'ai'"
              :class="message.from">Arfinnbot</span>
      </div>
    </div>

    <div class="flex-1"
         v-if="currentSlide === 0"></div>
    <input type="text"
           placeholder="Ask me anything!"
           class="chat-input"
           v-model="currentMessage"
           @input="currentSlide = 3"
           @keypress.enter="submitMessage"
           v-if="currentSlide === 0 || currentSlide === 3" />
    <p id="loading"
       v-if="loading">Arfinnbot is typing...</p>
    <button id="submit-btn"
            class="primary-btn"
            @click="submitMessage"
            v-if="currentSlide === 0 || currentSlide === 3">
      <svg xmlns="http://www.w3.org/2000/svg"
           fill="none"
           viewBox="0 0 24 24"
           stroke-width="2"
           stroke="currentColor"
           class="w-5 h-5">
        <path stroke-linecap="round"
              stroke-linejoin="round"
              d="M4.5 12h15m0 0l-6.75-6.75M19.5 12l-6.75 6.75" />
      </svg>
    </button>
  </div>
</template>

<style>
  #window {
		@apply w-screen md:w-full md:max-w-[24rem] h-full lg:max-h-[700px] bg-slate-50 rounded-none md:rounded-md fixed top-0 md:top-auto bottom-0 md:bottom-28 right-0 md:right-6 flex flex-col ml-0 md:ml-6;

		box-shadow: 0 0.125em 0.125em 0 rgb(0 0 0 / 14%), 0 0.0625em 0.3125em 0 rgb(0 0 0 / 12%), 0 0.1875em 0.0625em -0.125em rgb(0 0 0 / 20%);
  }

	#top-main {
		@apply bg-arfinnPurple-900 p-8 rounded-t-none md:rounded-t-md;
	}

  .main-head {
    @apply flex justify-between items-center;
  }

	h1 {
		@apply text-white font-bold text-3xl !important;
	}

	h2 {
		@apply text-white font-bold text-lg !important;
	}

	.subtitle {
		@apply text-slate-200 text-sm mt-4;
	}

	.big-button {
		@apply w-full px-4 py-3 border border-slate-200 rounded-md hover:bg-blue-50 bg-white cursor-pointer;
	}

	.button-title {
		@apply text-arfinnPurple-900 font-semibold text-sm;
	}

	.button-subtitle {
		@apply text-slate-700 text-sm;
	}

	.chat-input {
		@apply px-4 py-3 rounded-b-md border-b-0 border-l-0 border-r-0 border-t border-slate-200 focus:border-slate-200 focus:outline-none focus:ring-0 text-sm !important;
	}

	.text-input {
		@apply px-4 py-3 rounded-md border border-slate-200 focus:border-slate-200 focus:outline-none focus:ring-0 text-sm !important;
	}

	.primary-btn {
		@apply px-4 py-3 bg-arfinnPurple-900 text-center text-white font-semibold text-sm hover:bg-arfinnPurple-800 rounded-md cursor-pointer;
	}

	.message {
		@apply px-4 py-2 bg-slate-700 rounded-md relative w-5/6;
	}

	.message.ai {
		@apply bg-slate-600 text-right !important;
	}

  .message.ai:before {
    -webkit-mask-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0nMycgaGVpZ2h0PSczJyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnPjxwYXRoIGZpbGw9J2JsYWNrJyBkPSdtIDAgMyBMIDMgMyBMIDMgMCBDIDMgMSAxIDMgMCAzJy8+PC9zdmc+);
    mask-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0nMycgaGVpZ2h0PSczJyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnPjxwYXRoIGZpbGw9J2JsYWNrJyBkPSdtIDAgMyBMIDMgMyBMIDMgMCBDIDMgMSAxIDMgMCAzJy8+PC9zdmc+);
    left: -0.5rem !important;
    position: absolute;
    bottom: 0;
    height: 0.75rem;
    width: 0.75rem;
    background-color: inherit;
    content: "";
    -webkit-mask-size: contain;
    mask-size: contain;
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
    -webkit-mask-position: center;
    mask-position: center;

    /* flip horizontally */
    -webkit-transform: scaleX(1);
  }

  .message:before {
    -webkit-mask-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0nMycgaGVpZ2h0PSczJyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnPjxwYXRoIGZpbGw9J2JsYWNrJyBkPSdtIDAgMyBMIDMgMyBMIDMgMCBDIDMgMSAxIDMgMCAzJy8+PC9zdmc+);
    mask-image: url(data:image/svg+xml;base64,PHN2ZyB3aWR0aD0nMycgaGVpZ2h0PSczJyB4bWxucz0naHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmcnPjxwYXRoIGZpbGw9J2JsYWNrJyBkPSdtIDAgMyBMIDMgMyBMIDMgMCBDIDMgMSAxIDMgMCAzJy8+PC9zdmc+);
    right: -0.5rem !important;
    left: unset !important;
    position: absolute;
    bottom: 0;
    height: 0.75rem;
    width: 0.75rem;
    background-color: inherit;
    content: "";
    -webkit-mask-size: contain;
    mask-size: contain;
    -webkit-mask-repeat: no-repeat;
    mask-repeat: no-repeat;
    -webkit-mask-position: center;
    mask-position: center;

    /* flip horizontally */
    -webkit-transform: scaleX(-1);
  }

	.message-text {
		@apply text-sm text-white text-left;
	}

  span.user {
    @apply w-full text-right text-sm text-slate-500;
  }

  span.ai {
    @apply w-full text-left text-sm text-slate-500;
  }

  #submit-btn {
    @apply px-4 py-2 bg-arfinnPurple-900 text-center text-white font-semibold text-sm hover:bg-arfinnPurple-800 rounded-md cursor-pointer absolute bottom-1 right-1;
  }

  .wrapper-user {
    @apply flex flex-col items-end;
  }

  #loading {
    @apply absolute bottom-14 left-2 text-sm text-slate-500 bg-slate-50 px-2;
  }
</style>
